@import 'variables';

body {
  background: $dark url('../images/back-mobile.jpg') no-repeat 50% 0;
  background-size: 320px 464px;
}

@media (min-width: 660px) {
  body {
    background: $dark url('../images/back-tablet.jpg') no-repeat 50% 0;
    background-size: 660px 730px;
  }
}

@media (min-width: 1200px) {
  body {
    background: $dark url('../images/back-desktop.jpg') no-repeat 50% 0;
    background-size: 1200px 852px;
  }
}
